#grid {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
.row {
  flex-grow: 1;
  width: 100vw;
  display: flex;
  align-items: stretch;
}
.cell {
  width: calc(100vw / 100);
  height: calc(100vw / 100);
}
.alive-cell {
  background: rgb(75, 200, 255);
}
.dead-cell {
  background: rgba(0, 0, 0, 0.1);
}
